html * {
  outline: 1px solid red;
}

.container_slider_nakipy{
  margin:50px auto;width:300px;height:200px;overflow:hidden;position:relative
}
.photo_slider_nakipy{
  position:absolute;animation:round 15s infinite;opacity:0;width:100%
}

@keyframes round {
25%{opacity:1}
40%{opacity:0}
}
img:nth-child(1){animation-delay:12s}
img:nth-child(2){animation-delay:8s}
img:nth-child(3){animation-delay:4s}
img:nth-child(4){animation-delay:0}

@media(min-width:0px) and (max-width:320px) {
.container_slider_nakipy{width:80%;height:190px}
}
@media(min-width:321px) and (max-width:480px) {
.container_slider_nakipy{width:80%;height:190px}
}